

.user-edit{
    width: 100%;
    height: calc(100vh - 60px);
    background-color: var(--shark);
}

.user-edit__container{
    width: 100%;
    height: 100%;
    max-width: 900px;
    margin: 0 auto;
}

.user-edit__wrapper{
    width: 100%;
    height: 100%;
    background-color: var(--middle-shark);
    border-right: 1px solid var(--white01);
    border-left: 1px solid var(--white01);
    border-top: 1px solid var(--white01);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.user-edit__title{
    color: var(--white07);
    font-size: 18px;
    line-height: 24px;
    padding: 15px 0px 25px;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    font-weight: 700;
}

.user-edit__photo-editor{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

.user-edit__img-label{
    cursor: pointer;
    position: relative;
    min-width: 64px;
    min-height: 64px;
    max-height: 64px;
    max-width: 64px;
}

.user-edit__img-label img{
    border-radius: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.user-edit__text-label{
    cursor: pointer;
    user-select: none;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    line-height: 16px;
    font-weight: 400;
    color: var(--white);

    transition: .1s;
}

.user-edit__text-label:hover{
    color: var(--conifer);
}




#user-edit__input-img{
    display: none;
}

.add-photo-modal{
    visibility: hidden;
    opacity: 0;
}

/* .add-photo-modal .add-photo-modal__window{
    visibility: hidden;
    opacity: 0;
} */

.add-photo-modal{
    transition: .1s;
}

.add-photo-modal-active{
    visibility: visible;
    opacity: 1;
}


.user-edit__add-photo-modal{
    position: absolute;
    top: -60px;
    width: 100%;
    height: calc(100vh + 60px);
    z-index: 100;
    background-color: var(--black05);

    transition: .1s;
}

.add-photo-modal__window{
    position: absolute;
    z-index: 1000;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
    width: 95%;
    max-width: 360px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    background-color: var(--white);
    padding-bottom: 10px;

    transition: .1s;
}

.add-photo-modal__title{
    user-select: none;
    color: var(--black);
    font-size: 14px;
    line-height: 16px;
    padding: 10px 0px 10px;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    font-weight: 700;
}

.add-photo-modal__preview{
    width: 128px;
    height: 128px;
    border-radius: 50%;
}

.add-photo-modal__avatar{
    user-select: none;
    width: 290px;
    height: 290px;
    overflow: hidden;
}

.add-photo-modal__error-message{
    color: var(--red);
    user-select: none;
}

.add-photo-modal__save-btn{
    padding: 8px 10px 7px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 700;
    font-family: 'Open Sans', sans-serif;
    color: var(--white);
    background-color: var(--conifer);

    transition: .1s;
}

.add-photo-modal__save-btn:hover{
    background-color: var(--conifer09);
}

